import React from "react";
import { useLocation } from "react-router";
import { Link } from "react-router-dom";
import styled from "styled-components";

export const Footer = ({ className }) => {
  const exception = ["/login", "/signup"];
  const location = useLocation();
  if (exception.includes(location.pathname)) return null;
  return (
    <section className={`${className} footer container my-5 p-0`}>
      <div className="row py-5">
        <div className="col-8">
          <p className="fs__text fw-bold">CÔNG TY TNHH VIỆT NAM MASTERS © 2020</p>
          <div className="d-flex fs__details text-white-50">
            <div className="col-6">
              <p>Giấy chứng nhận đăng ký doanh nghiệp số: 0316390525</p>
              <p>Cấp ngày: 16/07/2020.</p>
              <p>Nơi cấp: Sở Kế hoạch và Đầu tư TP.Hồ Chí Minh.</p>
            </div>
            <div className="col-6">
              <p>
                <span>
                  <i className="fas fa-map-marker-alt"></i>
                </span>
                Lầu 2, Tòa nhà ITAXA, 19 Võ Văn Tần, P.6, Q.3, Thành phố Hồ Chí Minh
              </p>
              <p>
                <span>
                  <i className="fas fa-phone-alt"></i>
                </span>
                028 38220814
              </p>
            </div>
          </div>
        </div>
        <div className="footer__link col-4">
          <p className="fs__text fw-bold">Liên kết</p>
          <nav>
            <ul className="list-unstyled">
              <li>
                <Link to="/">Trang chủ</Link>
              </li>
              <li>
                <Link to="/#faq">Về chúng tôi</Link>
              </li>
              <li>
                <a href="">Bài viết</a>
              </li>
              <li>
                <a href="">FAQs</a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </section>
  );
};

export default styled(Footer)`
  & > .row {
    border-top: 2px solid;
    border-bottom: 2px solid;
    border-color: #ffc367;
    background-position: center center;
  }
  .footer__link a {
    color: white;
  }
`;
