import { lazy } from "react";

const Home = lazy(() => import("../../pages/Home/Home"));
const Login = lazy(() => import("../../pages/Login"));
const SignUp = lazy(() => import("../../pages/SignUp"));
const AgencyDashBoard = lazy(() => import("../../pages/AgencyDashBoard"));
const GettingReport = lazy(() => import("../../pages/GettingReport"));
const AgencyPolicy = lazy(() => import("../../pages/AgencyPolicy"));
const Blogs = lazy(() => import("../../pages/Blogs"));
const BlogDetail = lazy(() => import("../../pages/BlogDetail"));

const Routes = [
  { path: "/", exact: true, component: Home },
  { path: "/login", exact: true, component: Login },
  { path: "/signup", exact: true, component: SignUp },
  {
    path: "/agency-dashboard",
    exact: true,
    component: AgencyDashBoard,
    isPrivate: true,
    role: ["1"],
  },
  {
    path: "/getting-report",
    exact: true,
    component: GettingReport,
    isPrivate: true,
    role: ["0", "1"],
  },
  { path: "/agency-policy", exact: true, component: AgencyPolicy },
  { path: "/blogs", exact: true, component: Blogs },
  { path: "/blog/:slug", exact: true, component: BlogDetail },
];

export default Routes;
