import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";

const MobileMenu = ({ className, isOpen }) => {
  return (
    <section
      className={`${className} header navbar flex-column flex-lg-row p-5 p-lg-1 d-flex d-lg-none ${
        isOpen ? "show" : ""
      }`}
    >
      <div className="header__logo">
        <img src="/images/logo-footer.png" alt="Era Logo" />
      </div>
      {/* <div className="header__mobile-menu d-flex flex-column flex-lg-row justify-content-center justify-content-lg-between"> */}
      <div className="header__link">
        <nav className="d-flex flex-column flex-lg-row" style={{ columnGap: 4 }}>
          <Link to="/">Trang chủ</Link>
          <a href="">Về chúng tôi</a>
          <a href="">Bài viết</a>
          <a href="">FAQs</a>
        </nav>
      </div>
      <div className="header__button">
        <nav className="d-flex" style={{ columnGap: 4 }}>
          <Link to="/login" className="button-secondary fw-bold fw-bold">
            Đăng nhập
          </Link>
          <Link to="/signup" className="button-secondary--outline fw-bold">
            Đăng ký
          </Link>
        </nav>
      </div>
      {/* </div> */}
    </section>
  );
};

export default styled(MobileMenu)`
  transform: translateX(100%);
  transition: transform 0.3s ease-in;
  &.show {
    transform: translateX(0);
  }
  .header {
    &__mobile-menu {
    }
    &__logo {
      z-index: 3;
      > img {
        width: 200px;
      }
    }
    &__link a {
      font-weight: bold;
      color: white;
      margin: 0 10px;
    }
    &__link a:hover {
      font-weight: bolder;
      color: #ffd597;
    }
    &__button a {
      padding: 10px 20px;
    }
    &__button a:nth-child(1) {
      color: black;
    }
    &__button a:nth-child(2):hover {
      color: black;
    }
  }
  @media ${(props) => props.theme.breakPoints.xl} {
    z-index: 2;
    background-color: black;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    .header {
      &__mobile-menu {
        z-index: 2;
        background-color: black;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 20px;
        padding-top: 120px;
      }
      &__link {
        nav {
          row-gap: 16px;
        }
        a {
          font-weight: bold;
          color: white;
          margin: 0 10px;
          font-size: 24px;
        }
      }
    }
  }
`;
