import React, { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import styled from "styled-components";
import MobileNav from "../MobileNav";
import Hamburger from "hamburger-react";
import { useSelector } from "react-redux";
import UserInfo from "../UserInfoNav";

const Header = ({ className }) => {
  const exception = ["/login", "/signup"];
  const role = useSelector((state) => state.userStatus?.userInfo?.user?.role);
  const getNav = (role) => {
    const listNav = [
      [
        { name: "Trang chủ", link: "/" },
        { name: "Kích hoạt báo cáo", link: "/getting-report" },
        { name: "Về chúng tôi", link: "/" },
        { name: "Bài viết", link: "/blogs" },
        { name: "FAQs", link: "/" },
      ],
      [
        { name: "Trang chủ", link: "/" },
        { name: "Chính sách", link: "/agency-policy" },
        { name: "Kích hoạt báo cáo", link: "/getting-report" },
        { name: "Bài viết", link: "/blogs" },
        { name: "FAQs", link: "/" },
        { name: "DashBoard", link: "/agency-dashboard" },
      ],
    ];
    const defautNav = [
      { name: "Trang chủ", link: "/" },
      { name: "Về chúng tôi", link: "/" },
      { name: "Bài viết", link: "/blogs" },
      { name: "FAQs", link: "/" },
    ];
    return listNav[role] || defautNav;
  };

  const location = useLocation();
  const [isMobileNavOpen, setIsMobileNavOpen] = useState(false);
  const auth = useSelector((state) => state.userStatus.auth);
  const userInfo = useSelector((state) => state.userStatus.userInfo);
  // useEffect(() => {
  //   console.log(auth);
  //   console.log(userInfo);
  //   return () => {};
  // }, [auth, userInfo]);
  if (exception.includes(location.pathname)) return null;
  return (
    <>
      <section className={`${className} header navbar container`}>
        <div className="header__logo">
          <img src="/images/logo-footer.png" alt="Era Logo" />
        </div>
        <div className="header__link d-none d-lg-block">
          <nav className="d-flex flex-column flex-lg-row" style={{ columnGap: 4 }}>
            {getNav(role).map(({ name, link }) => (
              <Link key={name} to={link}>
                {name}
              </Link>
            ))}
          </nav>
        </div>
        {auth ? (
          <UserInfo {...userInfo?.user} />
        ) : (
          <div className="header__button d-none d-lg-block">
            <nav className="d-flex" style={{ columnGap: 4 }}>
              <Link to="/login" className="button-secondary fw-bold fw-bold">
                Đăng nhập
              </Link>
              <Link to="/signup" className="button-secondary--outline fw-bold">
                Đăng ký
              </Link>
            </nav>
          </div>
        )}

        <div className="header__mobile-menu__btn d-block d-lg-none d-flex align-content-center">
          <Hamburger toggled={isMobileNavOpen} toggle={setIsMobileNavOpen} duration={0.3} />
        </div>
      </section>
      <MobileNav isOpen={isMobileNavOpen} />
    </>
  );
};

export default styled(Header)`
  .header {
    &__mobile-menu {
      &__btn {
        z-index: 3;
      }
    }
    &__logo {
      > img {
        width: 200px;
      }
    }
    &__link a {
      font-weight: bold;
      color: white;
      margin: 0 10px;
      &:hover {
        font-weight: bolder;
        color: ${(props) => props.theme.colorSecondary};
      }
    }

    &__button {
      a {
        padding: 10px 20px;
        &:nth-child(1) {
          color: black;
        }
        &:nth-child(2):hover {
          color: black;
        }
      }
    }
  }
  /* @media ${(props) => props.theme.breakPoints.md} {
    z-index: 2;
    background-color: black;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    .header {
      &__mobile-menu {
        z-index: 2;
        background-color: black;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 20px;
        padding-top: 120px;
      }
      &__link {
        nav {
          row-gap: 16px;
        }
        a {
          font-weight: bold;
          color: white;
          margin: 0 10px;
          font-size: 24px;
        }
      }
    }
  } */
`;
