import React from "react";
import { useDispatch } from "react-redux";
import styled from "styled-components";
import { logout } from "../../../redux/action/userAction";

const UserInfo = ({ fullname, coin, className }) => {
  const dispatch = useDispatch();
  return (
    <div className={`${className} header__user`}>
      <a href="">
        <span>{fullname}</span>
        <img src="/images/meow.jpg" alt="Ảnh đại diện" />
      </a>
      <span className="logout__btn ms-2" onClick={() => dispatch(logout())}>
        Đăng xuất
      </span>
    </div>
  );
};

export default styled(UserInfo)`
  &.header {
    &__user {
      /* display: flex;
      flex-direction: row;
      vertical-align: center; */
      span {
        margin-right: 10px;
      }
      img {
        /* $size: 40px; */
        width: 40px;
        height: 40px;
        border-radius: 100%;
      }
    }
    .logout__btn {
      align-self: center;
    }
  }
`;
